<script>
import Vue from 'vue'
import $ from 'jquery'
import VueLazyload from 'vue-lazyload'

Vue.use(VueLazyload)

// or with options
Vue.use(VueLazyload, {
  preLoad: 1.3,
  error: 'dist/error.png',
  loading: 'dist/loading.gif',
  attempt: 1
})
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      img_position:'',
      before_x:[],
      before_y:[],
	  now_percent:0,
	  moved:false
    }
  },
  mounted () {
    // this.$refs.loading.addEventListener('animationend', () => {
    //   $('#loading').fadeOut()
    //   console.log('transition end')
    // })


	let timer = 0;
	setInterval(()=>{
		// seconds = timer
		timer++;
		if(timer>=5){
			console.log("XXXX");
			$("#loading").css("display","none")
		}
		if(timer > 6){
			this.moved = true
		}
	},1000)
    // console.log(this.$route.name)
  },



  methods:{


    mouse:function(el){
      // console.log(this.position.top);
      // console.log(el.pageX)
      // console.log(el.pageY)
      // console.log(this.$refs.picture2.clientHeight);
      // console.log(this.$refs.picture2.clientWidth);
	  let x;
	  let y;
      if(el.pageX < this.before_x.slice(-1) && this.moved){
        console.log("-");
		x = 10;
      }else{
		x = -10;
		  console.log("+");
      }

      if(el.pageY < this.before_y.slice(-1) && this.moved){
		y = -8
      }else{
	    y = 8
      }
      this.before_y.push(el.pageY);
      this.before_x.push(el.pageX);
	  this.img_position = "translateX(" + x + "%)translateY("+ y + "%)rotate(-127deg)scale(1.3)";

      console.log(this.left)


      // if(el.pageX + (this.$refs.picture2.clientWidth/2) < window.innerWidth){
      //   console.log("reu")
      //   this.position.left = el.pageX - (this.$refs.picture2.clientWidth/2) + "px";
      // }
      // if(el.pageY + (this.$refs.picture2.clientHeight/2) < window.innerHeight){
      //   console.log("ure")
      //   this.position.top = el.pageY - (this.$refs.picture2.clientHeight/2) + "px" ;
      // }
    },

	go(){
		window.location.href="https://110graduationproject.github.io/group/mainPage2.html";
	}
  }
}
</script>

