<template>
  <div id="app">
    <div id="loading" :style="">
      <img src="./assets/visual_picture/球體.png" alt="">
      <div class="percent">100</div>
      <h4 class="loading_txt"></h4>
	  </div>
    <!-- <img src="./assets/logo.png"> -->
    <router-view/>
  </div>
</template>

<script>
import $ from 'jquery'
export default {
  name: 'App',
  mounted(){
    $('.percent').each(function () {
      $(this).prop('Counter', 0).animate({
        Counter: $(this).text()
      }, {
        duration: 4000,
        easing: 'linear',
        step: function (now) {
		//   console.log(now)
          $(this).html(Math.ceil(now) + '%')
		  this.now_percent = now
		//   console.log(this.now_percent)
        }
      })
    })
  }
}
</script>

<style>
@import './assets/css/global.css';
@import './assets/css/index.css';
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  /* margin-top: 60px; */
}
body{
  padding: 0;
  margin: 0;
}

</style>



